.statsCard {
  padding: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  color: #fff;
  margin: 10px auto;
}

@media (max-width: 768px) {
  .statsCard {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 49%;
    flex: 1 1 49%;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
  }
}

@media (max-width: 420px) {
  .statsCard {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 99%;
    flex: 1 1 99%;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
  }
}
