.aboutMeSection {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.amelia {
  background-size: cover;
  background-position: right;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 50%;
  flex: 1 1 50%;
}

.sepia {
  -webkit-filter: sepia(1);
  filter: sepia(1);
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.aboutMeSection__info {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 50%;
  flex: 1 1 50%;
  background-color: #f5f5f5;
  padding: 135px 75px;
}

@media (max-width: 768px) {
  .amelia {
    height: 70vh;
    background-position: top;
    -webkit-box-flex: 1;
    -ms-flex: 1 1 100%;
    flex: 1 1 100%;
  }
  .aboutMeSection__info {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 100%;
    flex: 1 1 100%;
    padding: 100px 35px;
  }
}
